  setup() {
    return {};
  },
  data() {
    return {
      mdFile: "",
      numberPickerCount: 1,
      numberPickerCount2: 5
    };
  },
  async mounted() {
    this.mdFile = await fetch("/tailwind/numberPicker/README.md").then(res =>
      res.text()
    );
  }
});
