
import { defineComponent } from "vue";
import TRangeSlider from "@/components/tailwind/rangeSlider/TRangeSlider.vue";

export default defineComponent({
  name: "App",
  components: {
    TRangeSlider
  },
  setup() {
    return {};
  },
  data() {
    return {
      mdFile: "",
      rangeMax: 8000,
      rangeMin: 2000,
      rangeMax2: 8000,
      rangeMin2: 0,
      rangeMax3: 10000,
      rangeMin3: 2000,
      rangeMax4: 8000,
      rangeMin4: 2000
    };
  },
  async mounted() {
    this.mdFile = await fetch("/tailwind/rangeSlider/README.md").then(res =>
      res.text()
    );
  }
});
