
import { defineComponent } from "vue";
import TDropdown from "@/components/tailwind/dropdown/TDropdown.vue";

export default defineComponent({
  name: "App",
  components: {
    TDropdown
  },
  setup() {
    return {};
  },
  data() {
    return {
      mdFile: ""
    };
  },
  async mounted() {
    this.mdFile = await fetch("/tailwind/dropdown/README.md").then(res =>
      res.text()
    );
  }
});
