
import { defineComponent } from "vue";
import TBadge from "@/components/tailwind/badge/TBadge.vue";

export default defineComponent({
  name: "App",
  components: {
    TBadge
  },
  setup() {
    return {};
  },
  data() {
    return {
      mdFile: ""
    };
  },
  async mounted() {
    this.mdFile = await fetch("/tailwind/badge/README.md").then(res =>
      res.text()
    );
  }
});
