
import { defineComponent } from "vue";
import TAlert from "@/components/tailwind/alert/TAlert.vue";

export default defineComponent({
  name: "App",
  components: {
    TAlert
  },
  setup() {
    return {};
  },
  data() {
    return {
      showAlert: true,
      showAlert2: true,
      mdFile: ""
    };
  },
  async mounted() {
    this.mdFile = await fetch("/tailwind/alert/README.md").then(res =>
      res.text()
    );
  }
});
