
import { defineComponent } from "vue";
import TSearchable from "@/components/tailwind/searchable/TSearchable.vue";

export default defineComponent({
  name: "App",
  components: {
    TSearchable
  },
  setup() {
    return {};
  },
  data() {
    return {
      mdFile: "",
      searchableModel: "",
      searchableModel2: "",
      searchableModel3: "",
      searchableItems: []
    };
  },
  async mounted() {
    this.mdFile = await fetch("/tailwind/searchable/README.md").then(res =>
      res.text()
    );
  },
  methods: {
    searchAjax({ search, setLoading }): void {
      setLoading(true);
      fetch(
        `https://api.github.com/search/repositories?q=${escape(search)}`
      ).then(res => {
        console.log(res);
        res.json().then(json => {
          this.searchableItems = json.items;
          setLoading(false);
        });
      });
    }
  }
});
