
import { defineComponent } from "vue";
import TTextInput from "@/components/tailwind/text-input/TTextInput.vue";

export default defineComponent({
  name: "App",
  components: {
    TTextInput
  },
  setup() {
    return {};
  },
  data() {
    return {
      mdFile: "",
      myNumber: 0
    };
  },
  async mounted() {
    this.mdFile = await fetch("/tailwind/text-input/README.md").then(res =>
      res.text()
    );
  }
});
