
import { defineComponent } from "vue";
import TLoading from "@/components/tailwind/loading/TLoading.vue";

export default defineComponent({
  name: "App",
  components: {
    TLoading
  },
  setup() {
    return {};
  },
  data() {
    return {
      mdFile: ""
    };
  },
  async mounted() {
    this.mdFile = await fetch("/tailwind/loading/README.md").then(res =>
      res.text()
    );
  }
});
