
import { defineComponent } from "vue";
import TSkeleton from "@/components/tailwind/skeleton/TSkeleton.vue";

export default defineComponent({
  name: "App",
  components: {
    TSkeleton
  },
  setup() {
    return {};
  },
  data() {
    return {
      mdFile: ""
    };
  },
  async mounted() {
    this.mdFile = await fetch("/tailwind/skeleton/README.md").then(res =>
      res.text()
    );
  }
});
