
import { defineComponent } from "vue";
import TCard from "@/components/tailwind/card/TCard.vue";

export default defineComponent({
  name: "App",
  components: {
    TCard
  },
  setup() {
    return {};
  },
  data() {
    return {
      mdFile: ""
    };
  },
  async mounted() {
    this.mdFile = await fetch("/tailwind/card/README.md").then(res =>
      res.text()
    );
  }
});
