
import { computed, defineComponent, inject, PropType } from "vue";
import { variants } from "@/utility/css-helper";
import { useRenderClass } from "@/compositionFunctions/settings";
export default defineComponent({
  name: "TProgressBar",
  props: {
    value: {
      type: Number as PropType<number>,
      default: 0
    },
    max: {
      type: Number,
      default: () => inject("t-progress-bar-max", 100)
    },
    showPercent: {
      type: Boolean,
      default: () => inject("t-progress-bar-showPercent", false)
    },
    variant: {
      type: String,
      default: () => inject("t-progress-bar-variant", "primary"),
      validator: (propValue: string) => {
        // eslint-disable-next-line @typescript-eslint/ban-ts-ignore
        // @ts-ignore
        return !!variants[propValue];
      }
    }
  },
  setup(props) {
    const percent = computed(() => (props.value * 100) / props.max);
    const { renderClass } = useRenderClass("progressBar");
    return { percent, renderClass };
  }
});
