
import { defineComponent } from "vue";
import TZoom from "@/components/tailwind/zoom/TZoom.vue";

export default defineComponent({
  name: "App",
  components: {
    TZoom
  },
  setup() {
    return {};
  },
  data() {
    return {
      mdFile: ""
    };
  },
  async mounted() {
    this.mdFile = await fetch("/tailwind/zoom/README.md").then(res =>
      res.text()
    );
  }
});
