
import { defineComponent } from "vue";
import TPagination from "@/components/tailwind/pagination/TPagination.vue";

export default defineComponent({
  name: "App",
  components: {
    TPagination
  },
  setup() {
    return {};
  },
  data() {
    return {
      mdFile: "",
      pagination: 1
    };
  },
  async mounted() {
    this.mdFile = await fetch("/tailwind/pagination/README.md").then(res =>
      res.text()
    );
  }
});
