
import { defineComponent } from "vue";
import TCountDownTimer from "@/components/tailwind/timer/TCountDownTimer.vue";

export default defineComponent({
  name: "App",
  components: {
    TCountDownTimer
  },
  setup() {
    return {};
  },
  data() {
    return {
      mdFile: "",
      timer: 60 * 60 * 24 + 4
    };
  },
  async mounted() {
    this.mdFile = await fetch("/tailwind/timer/README.md").then(res =>
      res.text()
    );
  }
});
