
import { defineComponent } from "vue";
import TProgressBar from "@/components/tailwind/progress/TProgressBar.vue";

export default defineComponent({
  name: "App",
  components: {
    TProgressBar
  },
  setup() {
    return {};
  },
  data() {
    return {
      mdFile: ""
    };
  },
  async mounted() {
    this.mdFile = await fetch("/tailwind/progress/README.md").then(res =>
      res.text()
    );
  }
});
